import React from 'react';

interface LogoProps {
	style?: {};
	darkMode: boolean;
}

export default function JSXLogoFromStr(
	iconName: string,
	darkMode: boolean,
	key?: number,
	style?: {}
) {
	switch (iconName) {
		case 'Website':
			return <WebsiteIcon darkMode={darkMode} style={style} key={key} />;
		case 'Code':
			return <CodeIcon darkMode={darkMode} style={style} key={key} />;
		case 'Linkedin':
			return <LinkedinLogo darkMode={darkMode} style={style} key={key} />;
		case 'Github':
			return <GithubLogo darkMode={darkMode} style={style} key={key} />;
		case 'React':
			return <ReactLogo darkMode={darkMode} style={style} key={key} />;
		case 'Redux':
			return <ReduxLogo darkMode={darkMode} style={style} key={key} />;
		case 'SASS':
			return <SassLogo darkMode={darkMode} style={style} key={key} />;
		case 'Javascript':
			return <JavascriptLogo darkMode={darkMode} style={style} key={key} />;
		case 'Node':
			return <NodeLogo darkMode={darkMode} style={style} key={key} />;
		case 'HTML':
			return <HTMLLogo darkMode={darkMode} style={style} key={key} />;
		default:
			return <div style={style} key={key}></div>;
	}
}

